/* Apply smooth scrolling and default styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;
}

/* Set background color and layout */
body {
  background: #f9f9f9;
  color: #333;
  line-height: 1.6;
}

/* Container to center content and set max-width */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Navbar Styling */
.navbar {
  background: linear-gradient(to right, #6a11cb, #2575fc);
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
}

.navbar a {
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  padding: 10px 15px;
  transition: all 0.3s ease;
}

.navbar a:hover {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 5px;
}

/* Footer Styling */
.footer {
  background: #222;
  color: #fff;
  text-align: center;
  padding: 15px;
  font-size: 14px;
  margin-top: 40px;
}

/* Button Styles */
.button {
  display: inline-block;
  background: linear-gradient(to right, #6a11cb, #2575fc);
  color: white;
  padding: 12px 20px;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.button:hover {
  background: linear-gradient(to right, #2575fc, #6a11cb);
  transform: scale(1.05);
}

/* Page Layout */
.page-content {
  padding: 40px;
  background: white;
  border-radius: 12px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

/* Forms */
form {
  background: white;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

input, select {
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 16px;
}

input:focus {
  outline: none;
  border-color: #2575fc;
  box-shadow: 0px 0px 5px rgba(37, 117, 252, 0.5);
}

/* Responsive Design */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    text-align: center;
  }
  
  .container {
    padding: 10px;
  }
  
  .button {
    width: 100%;
  }
}
